import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";


gsap.registerPlugin(ScrollTrigger);

//text_ani = document.getElementsByClassName("text-anim");
//parcel build index.html --public-url ./

gsap.utils.toArray(".text-anim").forEach(section => {
  gsap.from(section.querySelectorAll(".row > span"), {
    scrollTrigger: section,
    y: "100%",
    duration: 0.4,
    stagger: 0.25
  })
});

gsap.utils.toArray(".feat-section").forEach(section => {
  gsap.from(section.querySelectorAll(".feat-logo"), {
    scrollTrigger: section,
    opacity: 0,
    duration: 0.2
  })
});




let tl_plat = gsap.timeline({
  scrollTrigger: {
    trigger: ".pla",
    //markers: true,
    start: "top center",
    end: "bottom center",
    toggleActions: "play pause resume reset"
  },
  //repeat: 0,
  //onComplete: pause_anims 
});

function pause_anims(){
  this.pause();
}

let pla_colors = ["#F46F25", "#1798C1", "#96BF48", "#FEFA49", "#0A5AFA", "#3EDC8B", "#FE6540"];

tl_plat.to(".main", {backgroundColor: pla_colors[0], duration: .5, ease: "linear"})
.to(".pla-slider > div", {x: "-100%", duration: 9, ease: "linear"}, "+=1")
.to(".main", {backgroundColor: pla_colors[1], duration: .5, ease: "linear"}, "-=9")
.to(".main", {backgroundColor: pla_colors[2], duration: .5, ease: "linear"}, "-=7")
.to(".main", {backgroundColor: pla_colors[3], duration: .5, ease: "linear"}, "-=4")
.to(".main", {backgroundColor: pla_colors[4], duration: .5, ease: "linear"}, "-=1")
.to(".main", {backgroundColor: pla_colors[5], duration: .5, ease: "linear"}, "+=1")
.to(".pla-slider > div", {x: "-133%", duration: 3, ease: "linear"}, "-=2")
.to(".main", {backgroundColor: pla_colors[6], duration: .5, ease: "linear"});


